//    
function LunaDate(year, month, day, leapMonth, ganji) {
    this.year = year;
    this.month = month;
    this.day = day;
    this.leapMonth = leapMonth;
    this.ganji = ganji;
}

//   
function lunarCalc(year, month, day, type, leapmonth) {

    var sakGanjiIndex;
    var yearGanjiIndex;
    var monthGanjiIndex;
    var dayGanjiIndex;
    var solYear;
    var solMonth;
    var solDay;
    var lunYear
    var lunMonth;
    var lunDay;

    // lunLeapMonth     
    // 1   0  
    var lunLeapMonth;
    var lunMonthDay;
    var lunIndex;

    var solMonthDay = [31, 0, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];

    /* range check */
    if (year < 1940 || year > 2101) {
        alert('1940 2101 ');
        return new LunaDate(0, 0, 0, 0, null);
    }

    /*        */
    if (year >= 2080) {
        /*   2080 1 1 ( 2079 12 10) */
        solYear = 2080;
        solMonth = 1;
        solDay = 1;
        lunYear = 2079;
        lunMonth = 12;
        lunDay = 10;
        lunLeapMonth = 0;
        solMonthDay[1] = 29; /* 2080  2 28 */
        lunMonthDay = 30; /* 2079 12 */
        sakGanjiIndex = 45;
        yearGanjiIndex = 35;
        monthGanjiIndex = 12;
        dayGanjiIndex = 54;
    } else if (year >= 2060) {
        /*   2060 1 1 ( 2059 11 28) */
        solYear = 2060;
        solMonth = 1;
        solDay = 1;
        lunYear = 2059;
        lunMonth = 11;
        lunDay = 28;
        lunLeapMonth = 0;
        solMonthDay[1] = 29; /* 2060  2 28 */
        lunMonthDay = 30; /* 2059 11 */
        sakGanjiIndex = 42;
        yearGanjiIndex = 15;
        monthGanjiIndex = 12;
        dayGanjiIndex = 9;
    } else if (year >= 2040) {
        /*   2040 1 1 ( 2039 11 17) */
        solYear = 2040;
        solMonth = 1;
        solDay = 1;
        lunYear = 2039;
        lunMonth = 11;
        lunDay = 17;
        lunLeapMonth = 0;
        solMonthDay[1] = 29; /* 2040  2 28 */
        lunMonthDay = 29; /* 2039 11 */
        sakGanjiIndex = 8;
        yearGanjiIndex = 55;
        monthGanjiIndex = 12;
        dayGanjiIndex = 24;
    } else if (year >= 2020) {
        /*   2020 1 1 ( 2019 12 7) */
        solYear = 2020;
        solMonth = 1;
        solDay = 1;
        lunYear = 2019;
        lunMonth = 12;
        lunDay = 7;
        lunLeapMonth = 0;
        solMonthDay[1] = 29; /* 2020  2 28 */
        lunMonthDay = 30; /* 2019 12 */
        sakGanjiIndex = 33;
        yearGanjiIndex = 35;
        monthGanjiIndex = 12;
        dayGanjiIndex = 39;
    } else if (year >= 2000) {
        /*   2000 1 1 ( 1999 11 25) */
        solYear = 2000;
        solMonth = 1;
        solDay = 1;
        lunYear = 1999;
        lunMonth = 11;
        lunDay = 25;
        lunLeapMonth = 0;
        solMonthDay[1] = 29; /* 2000  2 28 */
        lunMonthDay = 30;   /* 1999 11 */
        sakGanjiIndex = 30;
        yearGanjiIndex = 15;
        monthGanjiIndex = 12;
        dayGanjiIndex = 54;
    }
    else if (year >= 1970) {
        /*   1970 1 1 ( 1969 11 24) */
        solYear = 1970;
        solMonth = 1;
        solDay = 1;
        lunYear = 1969;
        lunMonth = 11;
        lunDay = 24;
        lunLeapMonth = 0;
        solMonthDay[1] = 28; /* 1970  2 28 */
        lunMonthDay = 30;   /* 1969 11 */
        sakGanjiIndex = 54;
        yearGanjiIndex = 45;
        monthGanjiIndex = 12;
        dayGanjiIndex = 17;
    }
    else {
        /*   1940 1 1 ( 1939 11 22) */
        solYear = 1940;
        solMonth = 1;
        solDay = 1;
        lunYear = 1939;
        lunMonth = 11;
        lunDay = 22;
        lunLeapMonth = 0;
        solMonthDay[1] = 29; /* 1940  2 28 */
        lunMonthDay = 29;   /* 1939 11 */
        sakGanjiIndex = 18;
        yearGanjiIndex = 15;
        monthGanjiIndex = 12;
        dayGanjiIndex = 39;
    }

    lunIndex = lunYear - LUNAR_LAST_YEAR;

    // type 1      
    // 2       
    //       1  
    //        
    while (true) {

        if (type == 1 && year == solYear && month == solMonth && day == solDay) {
            let ganji = {
                yearGanji: ganjiArray[yearGanjiIndex % 60],
                monthGanji: ganjiArray[monthGanjiIndex % 60],
                dayGanji: ganjiArray[dayGanjiIndex % 60],
                sakGanji: ganjiArray[sakGanjiIndex % 60]
            }
            return new LunaDate(lunYear, lunMonth, lunDay, lunLeapMonth, ganji);
        } else if (type == 2 &&
            year == lunYear &&
            month == lunMonth &&
            day == lunDay &&
            leapmonth == lunLeapMonth) {
            return new LunaDate(solYear, solMonth, solDay, 0, null);
        }

        //        
        if (solMonth == 12 && solDay == 31) {
            solYear++;
            solMonth = 1;
            solDay = 1;
            //   2   1 
            if (solYear % 400 == 0) {
                solMonthDay[1] = 29;
            } else if (solYear % 100 == 0) {
                solMonthDay[1] = 28;
            } else if (solYear % 4 == 0) {
                solMonthDay[1] = 29;
            } else {
                solMonthDay[1] = 28;
            }
        }
        //            1 
        else if (solMonthDay[solMonth - 1] == solDay) {
            solMonth++;
            solDay = 1;
        }
        else {
            solDay++;
        }

        //         
        if (lunMonth == 12 && ((lunarMonthTable[lunIndex][lunMonth - 1] == 1 && lunDay == 29) || (lunarMonthTable[lunIndex][lunMonth - 1] == 2 && lunDay == 30))) {
            lunYear++;
            lunMonth = 1;
            lunDay = 1;
            yearGanjiIndex++;
            monthGanjiIndex++;
            dayGanjiIndex++;
            sakGanjiIndex = dayGanjiIndex;
            if (lunYear > 2101) {
                alert("  .");
                break;
            }

            //   index 
            lunIndex = lunYear - LUNAR_LAST_YEAR;

            //  1 1 or 2  1 2 
            if (lunarMonthTable[lunIndex][lunMonth - 1] == 1) {
                lunMonthDay = 29;
            }
            else if (lunarMonthTable[lunIndex][lunMonth - 1] == 2) {
                lunMonthDay = 30;
            }
        }
        //     
        else if (lunDay == lunMonthDay) {

            //        
            if (lunarMonthTable[lunIndex][lunMonth - 1] >= 3 && lunLeapMonth == 0) {
                lunDay = 1;
                lunLeapMonth = 1;
                dayGanjiIndex++;
                sakGanjiIndex = dayGanjiIndex;
            }
            //        lunLeapMonth 
            else {
                lunMonth++;
                lunDay = 1;
                lunLeapMonth = 0;
                monthGanjiIndex++;
                dayGanjiIndex++;
                sakGanjiIndex = dayGanjiIndex;
            }

            //     
            if (lunarMonthTable[lunIndex][lunMonth - 1] == 1) {
                lunMonthDay = 29;
            }
            else if (lunarMonthTable[lunIndex][lunMonth - 1] == 2) {
                lunMonthDay = 30;
            }
            else if (lunarMonthTable[lunIndex][lunMonth - 1] == 3) {
                lunMonthDay = 29;
            }
            else if (lunarMonthTable[lunIndex][lunMonth - 1] == 4 && lunLeapMonth == 0) {
                lunMonthDay = 29;
            }
            else if (lunarMonthTable[lunIndex][lunMonth - 1] == 4 && lunLeapMonth == 1) {
                lunMonthDay = 30;
            }
            else if (lunarMonthTable[lunIndex][lunMonth - 1] == 5 && lunLeapMonth == 0) {
                lunMonthDay = 30;
            }
            else if (lunarMonthTable[lunIndex][lunMonth - 1] == 5 && lunLeapMonth == 1) {
                lunMonthDay = 29;
            }
            else if (lunarMonthTable[lunIndex][lunMonth - 1] == 6) {
                lunMonthDay = 30;
            }
        }
        else {
            lunDay++;
            dayGanjiIndex++;
        }
    }
    return new LunaDate(0, 0, 0, 0, null);
}

